.social-connects {
  width: 120px;
  height: 25px;
  /* background-color: aliceblue; */
  border-radius: 15px;
  gap: 5px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.social-icon {
  width: 25px;
  height: 25px;
  color: white;
  font-weight: 800;
  border-radius: 50%;
  background-color: white;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.social-connects a {
  color: white;
  font-weight: 400;
  text-decoration: none;
}
