* {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  height: 100vh;
  flex-grow: 1;
  size: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.forNavigation {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  padding: 60px 0 0 60px;
  box-sizing: border-box;
}

.dropDown {
  width: 60px;
  height: 60px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  gap: 5px;
  background-color: rgba(255, 255, 255, 0.2);
}

.dropDown hr {
  width: 30px;
  height: 2px;
  background-color: white;
  margin: 0;
  padding: 0;
  border: none;
}

.forBody {
  width: 70%;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.headShot {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 15px solid rgba(255, 255, 255, 0.5);
}

.content {
  flex-grow: 1;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  padding: 50px;
  box-sizing: border-box;
  color: white;
}

.content p {
  font-size: 1.5em;
  margin: 5px 0;
}

.content h1 span {
  color: rgb(4, 164, 204);
  font-size: 1.3em;
  font-weight: 700;
}

.content h2 span {
  color: rgb(4, 164, 204);
  font-size: 1.5em;
  font-weight: 700;
}

.connections {
  width: 100%;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  margin: 20px 0;
  gap: 10px;
}

.more-about-me {
  width: 200px;
  height: 50px;
  border-radius: 25px;
  background-color: rgb(4, 164, 204);
  border: 2px solid rgb(4, 164, 204);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-decoration: none;
  font-weight: 600;
}

.more-about-me:hover {
  background-color: grey;
  border: 2px solid grey;
}

.view-my-cv {
  width: 200px;
  height: 50px;
  border-radius: 25px;
  border: 2px solid rgb(4, 164, 204);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-decoration: none;
  font-weight: 600;
}

.view-my-cv:hover {
  background-color: rgb(4, 164, 204);
}
